import { Goal } from './goal';
import { DBAPI } from '../DBAPI.service';
import { Subscription } from 'rxjs/internal/Subscription';
import { URL } from '../ApiUrls';
import { Manager } from '../ObjectManager';
export class AddRemoveGoals extends Manager {
    //Metody Remove Edit i Get Dziedziczy od Klasy Manager



    goals: Goal[];
    goalSubscrion: Subscription;
    TodayDate: string;
    Sort(): void {
    }
    constructor(protected connectionAPI: DBAPI) {
        super(connectionAPI, URL.API_GOAL);
        this.Get();
    }
    AddGoal(title: string) {
        this.connectionAPI.addObjects(URL.API_GOAL, { 'title': title, 'id_user': 1 }).subscribe(res => {
            this.goals.push(res);
        }, console.error);
    }





}


