import { Injectable } from '@angular/core';
import { Manager } from '../ObjectManager';
import {URL} from '../ApiUrls'
import { DBAPI } from '../DBAPI.service';

@Injectable({
  providedIn: 'root'
})
export class RutineService extends Manager {
  Sort(): void {
  }

  constructor(private API:DBAPI) {
    super(API,URL.API_RUTINES);
   }

   Add(rutines){
     this.API.addObjects(URL.API_RUTINES,rutines).subscribe((data)=>{
      this.objects.push(data);
     });
   }

}
