import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes, CanActivate } from '@angular/router';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { ToDoComponent } from './to-do/to-do.component';
import { TaskManager } from './to-do/add-remove-task';
import { RutineComponent } from './rutine/rutine.component';
import { GoalsComponent } from './goals/goals.component';
import { TimeLogerComponent } from './time-loger/time-loger.component';
import { APP_BASE_HREF } from '@angular/common';
import { DBAPI } from './DBAPI.service';
import { HttpClientModule } from '@angular/common/http';

import {
  MatMenuModule,
  MatIconModule,
  MatButtonModule,
  MatInputModule,
  MatSelectModule,
  MatCheckboxModule
} from '@angular/material';
import { TaskComponent } from './task/task.component';
import { CommentComponent } from './comment/comment.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { SubTimerStartStopComponent } from './sub-timerStartStop/sub-timer.component';
import { TimerComponent } from './timer/timer.component';
import { SubTimerComponent } from './sub-timer/sub-timer.component';
import { SubTimerManager } from './subTimer.service';
import { LoginComponent } from './login/login.component';
import { AuthService } from './auth.service';
import { CalendarComponent } from './calendar/calendar.component';
import { DayComponent } from './day/day.component';
import { EventComponent } from './event/event.component';
import { RutinesElementComponent } from './rutines-element/rutines-element.component';

const appRoutes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'todo', component: ToDoComponent, canActivate: [AuthService] },
  { path: 'rutine', component: RutineComponent, canActivate: [AuthService] },
  { path: 'timer', component: TimeLogerComponent, canActivate: [AuthService] },
  { path: 'goals', component: GoalsComponent, canActivate: [AuthService] },
  { path: 'calendar', component: CalendarComponent, canActivate: [AuthService] },
  {
    path: '',
    redirectTo: '/todo',
    pathMatch: 'full',
    canActivate: [AuthService]
  }
];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    ToDoComponent,
    RutineComponent,
    GoalsComponent,
    TimeLogerComponent,
    TaskComponent,
    CommentComponent,
    SubTimerStartStopComponent,
    TimerComponent,
    SubTimerComponent,
    LoginComponent,
    CalendarComponent,
    DayComponent,
    EventComponent,
    RutinesElementComponent
  ],
  imports: [
    FormsModule,
    BrowserModule,
    OwlDateTimeModule,
    BrowserAnimationsModule,
    OwlNativeDateTimeModule,
    HttpClientModule,
    MatMenuModule,
    MatIconModule,
    MatButtonModule,
    MatSelectModule,
    MatCheckboxModule,
    MatInputModule,
    RouterModule.forRoot(appRoutes, { enableTracing: true }),
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: environment.production
    })
  ],
  providers: [
    DBAPI,
    TaskManager,
    SubTimerManager,
    { provide: APP_BASE_HREF, useValue: './' }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor() {}
}
