import { Manager } from './ObjectManager';
import { DBAPI } from './DBAPI.service';
import { URL } from './ApiUrls';
import { SubTimer } from './sub-timerStartStop/subTimer';
import { Injectable } from '@angular/core';
@Injectable()

export class SubTimerManager extends Manager {

    subTimers: SubTimer[][];
    public RunningTimer: SubTimer;
    constructor(private apiService: DBAPI) {
        super(apiService, URL.API_SUB_TIMER);
        this.RunningTimer = null;

    }
    Sort(): void {
    }

    Add(task_ID): any {
        this.objects.map((el) => {
            if (el.stop === null) {
                el.stop = Date.now();
            }
        });
        if (task_ID !== undefined) {
            this.apiService.addObjects(URL.API_SUB_TIMER, { 'start': new Date(), 'id_task': task_ID }).subscribe((data) => {
                this.objects.push(data);
            });
        }
    }
    getRunningTimer(task_ID) {
        this.apiService.getObjects(URL.API_SUB_TIMER + '/' + task_ID).subscribe((data) => {
            if (data.id !== undefined) {
                this.RunningTimer = data;
            }

        });
    }
}
