import { Component, OnInit, Input } from '@angular/core';
import { SubTimer } from '../sub-timerStartStop/subTimer';
import { DBAPI } from '../DBAPI.service';
import { SubTimerManager } from '../subTimer.service';

@Component({
  selector: 'app-sub-timer',
  templateUrl: './sub-timer.component.html',
  styleUrls: ['./sub-timer.component.css'],
})
export class SubTimerComponent implements OnInit {
  @Input() subTimer: SubTimer;
  start: Date;
  stop: Date;
  constructor(private subTimerService: SubTimerManager) {

  }

  ngOnInit() {

    this.start = new Date(this.subTimer.start);
    this.stop = new Date(this.subTimer.stop);

  }
  StopDataChange() {
    const SettingDate = this.stop;
    const currentDate = new Date(this.subTimer.start);
    // Ustawia rok miesic i dzien pobrany z daty startu
    SettingDate.setFullYear(currentDate.getFullYear());
    SettingDate.setMonth(currentDate.getMonth());
    SettingDate.setDate(currentDate.getDate());
    this.subTimer.stop = SettingDate;
    
    this.subTimerService.Edit(this.subTimer);


  }
  StartDataChange() {
    const SettingDate = this.start;
    const currentDate = new Date(this.subTimer.start);
        // Ustawia rok miesic i dzien pobrany z daty startu

    SettingDate.setFullYear(currentDate.getFullYear());
    SettingDate.setMonth(currentDate.getMonth());
    SettingDate.setDate(currentDate.getDate());    
    this.subTimer.start = SettingDate;

    this.subTimerService.Edit(this.subTimer);


  }
  DataChange(el: HTMLInputElement) {
    this.subTimer.stop = new Date(el.value);

  }
}
