import { Component, OnInit } from '@angular/core';
import { SubTimerManager } from '../subTimer.service';
import { SubTimer } from '../sub-timerStartStop/subTimer';

@Component({
  selector: 'app-time-loger',
  templateUrl: './time-loger.component.html',
  styleUrls: ['./time-loger.component.css'],
  providers: [SubTimerManager]
})
export class TimeLogerComponent implements OnInit {
  subTimers: SubTimer[][];
  constructor(private TM: SubTimerManager) {
    this.TM.Get('/TaskTitle');
  }

  groupSubTimersByDay() {
    this.subTimers = undefined;
    this.TM.objects.map(
      (el) => {
        if (this.subTimers === undefined) {
          this.subTimers = new Array();
        }
        let elDate;
        elDate = new Date(new Date(el.start).getFullYear(), new Date(el.start).getMonth(), new Date(el.start).getDate()).toISOString().substring(0, 10);
        if (elDate in this.subTimers) {
          this.subTimers[elDate].push(el);
        }
        else {
          this.subTimers.push(elDate);
          this.subTimers[elDate] = new Array();
          this.subTimers[elDate].push(el);
        }
      }
    );
    return this.subTimers;
  }

  ngOnInit() {
    this.groupSubTimersByDay();
   

  }

}
