import { Component, OnInit, Input } from '@angular/core';
import { RutinesObj } from './rutines';
import { RutineService } from '../rutine/rutine.service';
@Component({
  selector: 'app-rutines-element',
  templateUrl: './rutines-element.component.html',
  styleUrls: ['./rutines-element.component.css']
})
export class RutinesElementComponent implements OnInit {
  @Input() rutinesObject: RutinesObj;

  constructor(private service: RutineService) {}
  ngOnInit() {}
  Add(id_task: number, recursing_day: Array<number>) {
    this.service.Add(
      new RutinesObj(null, id_task, null, JSON.stringify(recursing_day))
    );
  }

  Edit(rutine: RutinesObj) {
    this.service.Edit(rutine);
  }
  Delete(id_rutine: number) {
    this.service.Remove(id_rutine);
  }
}
