import { Injectable } from '@angular/core';
import { Manager } from '../ObjectManager';
import { DBAPI } from '../DBAPI.service';
import { URL } from '../ApiUrls';

@Injectable({
  providedIn: 'root'
})
export class CalendarService extends Manager {
  Sort(): void {}

  constructor(connectionAPI: DBAPI) {
    super(connectionAPI, URL.API_EVENT);
    this.Get();
  }
  Add(title: string, date: Date, time: string) {
    this.connectionAPI
      .addObjects(URL.API_EVENT, {
        title: title,
        date: date,
        time: "15:45",
        color: '#fff'
      })
      .subscribe(res => {
        this.objects.push(res);
      });
  }
}
